code[class*="language-"],
pre[class*="language-"] {
  -moz-tab-size: 2;
  tab-size: 2;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: normal;
  font-family: Menlo, Monaco, Courier New, monospace;
  font-size: 14px;
  color: #fff;
  text-shadow: none;
}
:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background: #2a2a2a;
}
pre[class*="language-"] {
  padding: 15px;
  border-radius: 4px;
  border: 1px solid #e1e1e8;
  overflow: auto;
  position: relative;
}
pre[class*="language-"] code {
  white-space: pre;
  display: block;
}
:not(pre) > code[class*="language-"] {
  padding: 0.15em 0.2em 0.05em;
  border-radius: 0.3em;
  border: 0.13em solid #7a6652;
  box-shadow: inset 1px 1px 0.3em -0.1em #000;
}
.token.namespace {
  opacity: 0.7;
}
.token.cdata,
.token.comment,
.token.doctype,
.token.prolog {
  color: #6f705e;
}
.token.boolean,
.token.number,
.token.operator {
  color: #a77afe;
}
.language-css .token.string,
.style .token.string,
.token.attr-name,
.token.entity,
.token.string,
.token.url {
  color: #e6d06c;
}
.token.inserted,
.token.selector {
  color: #a6e22d;
}
.token.atrule,
.token.attr-value,
.token.deleted,
.token.important,
.token.keyword {
  color: #ef3b7d;
}
.token.regex,
.token.statement {
  color: #76d9e6;
}
.token.placeholder,
.token.variable {
  color: #fff;
}
.token.bold,
.token.important,
.token.statement {
  font-weight: 700;
}
.token.punctuation {
  color: #bebec5;
}
.token.entity {
  cursor: help;
}
.token.italic {
  font-style: italic;
}
code.language-markup {
  color: #f9f9f9;
}
code.language-markup .token.tag {
  color: #ef3b7d;
}
code.language-markup .token.attr-name {
  color: #a6e22d;
}
code.language-markup .token.attr-value {
  color: #e6d06c;
}
code.language-markup .token.script,
code.language-markup .token.script .token.keyword,
code.language-markup .token.style {
  color: #76d9e6;
}
pre[class*="language-"][data-line] {
  position: relative;
  padding: 1em 0 1em 3em;
}
pre[data-line] .line-highlight {
  position: absolute;
  left: 0;
  right: 0;
  padding: 0;
  margin-top: 1em;
  background: hsla(0, 0%, 100%, 0.08);
  pointer-events: none;
  line-height: inherit;
  white-space: pre;
}
pre[data-line] .line-highlight:before,
pre[data-line] .line-highlight[data-end]:after {
  content: attr(data-start);
  position: absolute;
  top: 0.4em;
  left: 0.6em;
  min-width: 1em;
  padding: 0.2em 0.5em;
  background-color: hsla(0, 0%, 100%, 0.4);
  color: #000;
  font: 700 65%/1 sans-serif;
  height: 1em;
  line-height: 1em;
  text-align: center;
  border-radius: 999px;
  text-shadow: none;
  box-shadow: 0 1px 1px hsla(0, 0%, 100%, 0.7);
}
pre[data-line] .line-highlight[data-end]:after {
  content: attr(data-end);
  top: auto;
  bottom: 0.4em;
}
